import React, { Component } from 'react';
import './style.css'
class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            contentvideos: [],
            videoselect: [],
            checkdata: false
        }
    }
    componentWillMount() {

        fetch('http://huuhao.me:8080/getinfodowntoday')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                if (myJson != null) {
                    this.setState({
                        checkdata: true
                    })
                }
                this.setState({
                    contentvideos: myJson,
                    videoselect: myJson[0]
                })
            });
    }
    counttodaydown() {
       
        let datenow = getdatenow();
        console.log(datenow);
        let count = 0;
        this.state.contentvideos.map((item, index) => {
            if (Number(item.dateexport) === Number(datenow)) {
                count++;
            }

        });
        return count;

    }
    counttodaydownqua() {
       
        let datenow = getdatenow();
        console.log(datenow);
        let count = 0;
        this.state.contentvideos.map((item, index) => {
            if (Number(item.dateexport) === (Number(datenow)-1)) {
                count++;
            }

        });
        return count;

    }
    counttodaydownkia() {
       
        let datenow = getdatenow();
        console.log(datenow);
        let count = 0;
        this.state.contentvideos.map((item, index) => {
            if (Number(item.dateexport) === (Number(datenow)-2)) {
                count++;
            }

        });
        return count;

    }
    render() {
        var x=this.counttodaydown();
        var x1=this.counttodaydownqua();
        var x2=this.counttodaydownkia();
        var element1 = this.state.contentvideos.map((item, index) => {

            return <tr>
                <td>{item.id}</td>
                <td>{item.idvideo}</td>
                <td>{item.videosname}</td>
                <td>{item.datevideo}</td>
                <td>{item.like}</td>
                <td>{item.views}</td>
                <td>{item.idchannel}</td>
            </tr>;
        });

        return (


            <div >
<h3 className="editcenter1">Kết quả tải được 3 ngày gần nhất</h3>
<h5 className="editcenter1">Hôm nay tải được tính đến hiện tại: {x}</h5>
<h5 className="editcenter1">Số video ngày {getdateStrinh(1)+" tải được"}: {x1}</h5>
<h5 className="editcenter1">Số video ngày {getdateStrinh(2)} tải được: {x2}</h5>
                <table className="editcenter" >
               
                    <td>ID</td>
                    <td>VideoID</td>
                    <td>Video Name</td>
                    <td>Date downloaded</td>
                    <td>Like</td>
                    <td>View</td>
                    <td>Idchannel</td>
                    {element1}




                </table>
            </div>
        )
    }
}
function getdatenow() {
    var today = new Date();
    var datetemp;
    //console.log("date",today.getDate());
    if (Number(today.getMonth()) >= 10 && Number(today.getDate()) >= 10)
        datetemp = today.getFullYear() + (today.getMonth() + 1) + today.getDate();
    if (Number(today.getMonth()) < 10 && Number(today.getDate()) > 10)
        datetemp = today.getFullYear() + '0' + (today.getMonth() + 1) + today.getDate();
    if (Number(today.getMonth()) >= 10 && Number(today.getDate()) < 10)
        datetemp = today.getFullYear() + (today.getMonth() + 1) + '0' + today.getDate();
    if (Number(today.getMonth()) < 10 && Number(today.getDate()) < 10)
        datetemp = today.getFullYear() + '0' + (today.getMonth() + 1) + '0' + today.getDate();
    //console.log(datetemp);
    return datetemp;
}
function getdateStrinh(nub) {
    var today = new Date();
    var datetemp;
        datetemp =Number(today.getDate()-Number(nub))+"/"+(today.getMonth() + 1) +"/"+ today.getFullYear();
    return datetemp;
}


export default Home;
